import {
    Alert,
	Card,
	CardContent,
	Divider,
	Grid,
	styled,
	Typography,
} from "@mui/material";
import DOMPurify from "dompurify";
import Markdown from "marked-react";
import { useTranslation } from "react-i18next";

import { useTask } from "../../../../Application";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";
import ReceiveBirthdayMessageForm from "./ReceiveBirthdayMessageForm";
import BirthdayMessageForm from "./BirthdayMessageForm";

const MarkdownContainer = styled("div")(({ theme }) => ({
	"& h3": {
		marginTop: 0,
	},
}));

const Birthday = () => {
	const { t } = useTranslation(["customers-club", "global"]);

	const task = useTask("Customers-Club.birthday");

	const GUIDE = `
### نکات آموزشی

برای استفاده از افزونه تبریک تولد دو کار باید صورت بگیرد:
1. دریافت تاریخ تولد مشتری
2. تنظیم متن تبریک تولد

#### 2 روش برای دریافت تاریخ تولد مشتری
برای گرفتن تاریخ تولد از مشتری دو روش وجود دارد:

الف) دریافت تاریخ تولد از طریق دستگاه:
با این روش می‌توانید از مشتریانی که به صورت حضوری در مجموعه شما حاضر هستند، درخواست کنید که تاریخ تولد خود را بر روی دستگاه وارد کنند.
اگر این روش را انتخاب کنید کادر مربوط به متن "درخواست تاریخ تولد" را مشاهده نخواهید کرد.

نکته:
در صورتی‌ که قصد داشتید از طریق دستگاه تاریخ تولد مشتری را دریافت کنید، به افزونه یادآوری بروید و چک کنید که اگر دریافتِ تاریخ یادآوری هم از طریق دستگاه است حتما آن را غیر فعال کنید. چرا که به صورت همزمان نمی‌توان هر دو تاریخ تولد و یادآوری را از طریق دستگاه فعال گذاشت.

ب) دریافت تاریخ تولد از طریق پیامک:
با این روش، در کادر "پیامک دریافت تاریخ تولد" متنی را به منظور گرفتن تاریخ تولد از مشتری وارد کنید و در انتهای این متن بر روی آیکون "تاریخ تولد" کلیک کنید تا یک لینک به انتهای پیامک اضافه شود.
زمانی که این پیام برای مشتری ارسال شد، او با کلیک بر روی این لینک تاریخ تولد خود را وارد می‌کند.
مشتری بعد از اینکه تاریخ تولد خود را وارد کرد، شما در منوی مشتریان می‌توانید تاریخ‌ تولد آنها را ببینید.

#### ارسال تبریک تولد
بعد از تنظیم نحوه دریافت تاریخ تولد، نوبت به وارد کردن متن مناسب برای تبریک گفتن می‌رسد.
یک متن جذاب به مناسبت تولد مشتریان در کادر دوم وارد کنید تا در روزی که تولد مشتری است، ارسال شود.

کلیه پیامک های این قسمت در بازه زمانی 8 صبح تا 11 شب ارسال می شوند ، مثال اگر مشتری ساعت 10 شب شماره اش را وارد دستگاه کند و شما تعریف کرده باشید که 2 ساعت بعد پیامک نظرسنجی برای او ارسال شود ، پیامک ساعت 8 صبح روز بعد برای مشتری ارسال می شود.
`;

	return (
		<TabView {...task}>
			<ResponsiveContainer>
				<Card variant="outlined" sx={{ mb: 3 }}>
					<CardContent sx={{ p: 3 }}>
						<Grid container spacing={3}>
							<Grid item xs={12} md={6}>
                                <Typography variant="h6" mb={1}>{t("pages.birthday.birthday_recieve_message_title")}</Typography>
                                <Typography color="text.secondary" mb={1}>{t("pages.birthday.birthday_recieve_message_description")}</Typography>

								<ReceiveBirthdayMessageForm />

								<Divider sx={{ my: 3 }} />

                                <Typography variant="h6" mb={1}>{t("pages.birthday.birthday_message_title")}</Typography>
                                <Typography color="text.secondary" mb={1}>{t("pages.birthday.birthday_message_description")}</Typography>

								<BirthdayMessageForm />
							</Grid>
							<Grid item xs={12} sm={6}>
                                <Alert color="info">
                                    <MarkdownContainer>
                                        <Markdown breaks={true}>
                                            {DOMPurify.sanitize(GUIDE)}
                                        </Markdown>
                                    </MarkdownContainer>
                                </Alert>
							</Grid>
						</Grid>
					</CardContent>
				</Card>
			</ResponsiveContainer>
		</TabView>
	);
};

export default Birthday;
